.container-image{
   width: 100%;
   height: 100%;
   object-fit: contain;

}

.container-modal{
    width: 700px;
    height: 600px;
    background-color: #fff;
}

@media only screen and (max-width: 770px) {
    .container-image{
        width: 100%;
        height: 100%;
        object-fit: contain;
     
     }
     
     .container-modal{
         width: 300px;
         height: 500px;
         background-color: #fff;
     }
}