
.footer-container {
    font-family: "Roboto", sans-serif;
    background-color: #1d262d;
    font-size: 14px;
}

.footer-container a {
    color: inherit;
    text-decoration: none;
}


.footer-container .items-container {
    display: grid;
    grid-template-columns: 230px 1fr 1fr;
    column-gap: 10px;
    color: #8b9ead;
    padding: 15px 15px;
}

.footer-container .item-title {
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    color: #dcdddf;
    text-transform: uppercase;
    margin-bottom: 25px;
}

.footer-container .items-with-icons > div {
    display: flex;
    align-items: center;
}

.footer-container .items-with-icons > div a {
    display: flex;
    align-items: center;
}

.footer-container .items-with-icons > div a p {
    margin-left: 10px;
}

.copyright {
    font-family: "Roboto", sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #293740;
    color: #617d95;
    font-weight: 500;
    height: 56px;
    width: 100%;
}


@media only screen and (max-width: 768px) {

    .footer-container .items-container > div:nth-child(1) {
        display: none;
    }

    .footer-container .items-container {
        display: block;
        padding: 15px 30px;
        
    }

    .footer-container .item-title {
        
        margin-bottom: 10px;
    }

    .footer-container > div {
        margin-top: 30px;
    }

    .footer-container .items-container > div {
        margin-bottom: 30px;
    }

    .footer-container .items-container > div:last-child {
        margin-bottom: 0px;
    }

}