#contact-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 100%;
    background-color:#191e4e;
    /* overflow-y: auto; */
    user-select: none;
}


#contact-container .contact-actions {
    display: flex;
    width: 70%;
    height: 80px;
    margin-top: 20px;
    margin-bottom: 20px;
}

#contact-container .contact-actions .icon {
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
    height: 100%;
    width: calc(100% / 3);
    border-radius: 5px;
}

#contact-container .contact-actions .icon p{
    margin-top: 13px;
    margin-left: 5px;
    color: #fff;  
}

#contact-container .contact-actions .icon:hover{
    background-color: rgba(255, 255, 255, 0.2);
}

#contact-container .content{
    display: flex;
    flex-direction: column;
    width: 70%;
    border-radius: 20px;
    background-color: rgb(255, 255, 255);
    padding: 30px 30px;
    
}

#contact-container .content > *{
   margin: 10px 0px;
}

@media only screen and (max-width: 768px){
    .contact-actions{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        width: 70%;
        border-radius: 20px;
        padding: 30px 30px;
        
    }
    .icon {
        display: flex;
        /* flex-direction: column; */
        align-items: center;
        justify-content: center;
        /* height: 100%;
        width: calc(100% / 3); */
        border-radius: 5px;
    }
}


