/* ********************************** CAROUSEL *************************************** */
/* *********************************************************************************** */
/* *********************************************************************************** */
/* *********************************************************************************** */


.carousel-container {
    position: relative;
    background-color: rgba(0, 0, 0, 0.1);
    overflow: hidden;
}


.carousel-container .indicator {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 50px;
    /* background-color: rgba(255, 255, 255, 0.8); */
    /* border: 1px solid #000; */
    z-index: 5;
}

.carousel-container .indicator span {
    width: 8px;
    height: 8px;
    background-color: rgba(255, 255, 255, 0.5);
    margin: 0 5px;
    border-radius: 4px;
}

.carousel-container .indicator span.selected {
    background-color: #FFF;
}





/* *********************************************************************************** */
/* *********************************************************************************** */
/* *********************************************************************************** */
/* **************************** CAROUSEL CONTAINER *********************************** */
/* *********************************************************************************** */
/* *********************************************************************************** */
/* *********************************************************************************** */

.carousel-items {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* animation: SlideOut 2s; */

}

.carousel-items img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* *********************************************************************************** */
/* *********************************************************************************** */
/* *********************************************************************************** */
/* **************************** CAROUSEL ANIMATION *********************************** */
/* *********************************************************************************** */
/* *********************************************************************************** */
/* *********************************************************************************** */

.slideOut {
    animation: SlideOut 2s;
    animation-fill-mode: both;
    z-index: 2;
}

.slideIn {
    animation: SlideIn 2s;
    animation-fill-mode: both;
    z-index: 2;
}


@keyframes SlideOut {
    from {
        transform: translateX(0);
    } to {
        transform: translateX(-100%);
    }
}

@keyframes SlideIn {
    from {
        transform: translateX(100%);
    } to {
        transform: translateX(0);
    }
}