
.side-bar {
    position: fixed;
    top: 0;
    left: 0;
    padding-right: 20px;
    background-color: rgba(0, 0, 0, 0.904);
    width: 100vw;
    height: 100vh;
    z-index: 200;
}

.side-bar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 64px;
    margin: 0 15px;
}

