.home-body{
    overflow-y: auto;
}


.carousel-label {
    position: absolute;
    bottom: 50px;
    left: 0;
    color: #FFF;
    margin: 0 30px;
    /* border: 1px solid #000; */
}

.carousel-label p:nth-child(1) {
    font-size: 32px;
}

.carousel-label p:nth-child(2) {
    /* font-size: 32px; */
    color: rgba(255, 255, 255, 0.6);
}

#scrollable-container{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 60px;
   
}


#projets-card-conatiner {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 20px;
    row-gap: 20px;
}


@media only screen and (max-width: 768px) {
    #projets-card-conatiner {
        grid-template-columns: 1fr;
        row-gap: 20px;
    }
}

@media screen and (min-width: 768px) and (max-width: 900px) {
    #projets-card-conatiner {
        grid-template-columns: 1fr 1fr;
        column-gap: 10px;
        row-gap: 20px;
    }
}

@media screen and (min-width: 900px){
    #projets-card-conatiner {
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 20px;
        row-gap: 20px;
    }
}



