
#scrollable-container{
    display: flex;
    align-items: center;
    justify-content: center;
}

#projets-card-conatiner {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 20px;
    row-gap: 20px;
    margin-left: 25px;
    margin-right: 25px;
}



.mobile-select-category {
    display: none;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 15px;
    /* backgound-color: #000; */
}

.mobile-select-category > * {
    width: 80%;
}



/* ########################################################################## */
/* ########################################################################## */
/* ############################# CATEGORIES ################################# */
/* ########################################################################## */
/* ########################################################################## */

#categories,
#categories-height{
    height: 60px;
    width: 100%;
    margin-bottom: 20px;
    background-color: #0B0F32;
}

#categories {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 10;
    /* background-color: purple; */
    height: 50px;
    width: 100%;
    padding-left: 20px;
    
}

#categories > .category {
    font-size: 22px;
    font-weight: 600;
    /* color:rgba(11, 15, 50, 0.3); */
    color: rgba(180, 179, 179, 0.952);
    /* color: rgba(255, 255, 255, 0.9); */
    margin-left: 90px;
    margin-right: 40px;
    cursor: pointer;
}

#categories > .category.selected {
    position: relative;
    /* color:  #0B0F32; */
    color: #FFF;
}

#categories > .category.selected:before {
    position: absolute;
    bottom: -2px;
    content: '';
    background-color: #7854f6;
    width: 100%;
    height: 3px;
    border-radius: 10px;
}



@media only screen and (max-width: 770px) {
    .mobile-select-category {
        display: block;
    }

    #categories {
        display: none;
    }
}

@media only screen and (max-width: 768px) {
    #projets-card-conatiner {
        grid-template-columns: 1fr;
        row-gap: 20px;
    }
}

@media screen and (min-width: 768px) and (max-width: 900px) {
    #projets-card-conatiner {
        grid-template-columns: 1fr 1fr;
        column-gap: 10px;
        row-gap: 20px;
    }
}

@media screen and (min-width: 900px){
    #projets-card-conatiner {
        grid-template-columns: 1fr 1fr 1fr;
        column-gap: 20px;
        row-gap: 20px;
    }
}
