.nav-bar-container, 
.nav-bar-container-height {
    height: 64px;
    width: 100%;
}

.nav-bar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    background-color: rgb(255, 255, 255);
    /* width: 100%; */
    padding: 0 40px;
    z-index: 10;
    /* background-color: #000; */
    border: 0px solid #000;
    box-sizing: border-box;
}

.mobile-menu-icon {
    display: none;
}

.navigation {
    display: flex;
    /* background-color: blue; */
}

.navigation .li {
    font-family: "Roboto", sans-serif;
    display: inline-block;
    position: relative;
    padding: 0 30px;
    font-size: 16px;
    font-weight: 500;
    color: #000;
    cursor: pointer;
    transition: color 0.6s;
    /* background-color: red; */
    height: 100%;
    /* border-bottom: 2px solid blue;
    box-sizing: content-box; */
}

.navigation .li.selected::before {
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background-color:#2196f3;
}

.navigation .li:hover {
    /* background-color: rgba(0, 0, 0, 0.1); */
    color: #A2A2A2;
}
 

/* ########################################################################## */
/* ########################################################################## */
/* ############################# MOBILE ################################# */
/* ########################################################################## */
/* ########################################################################## */


@media only screen and (max-width: 768px){

    .nav-bar-container {
        padding: 0 15px;
    }

    .navigation {
        display: none;
    }

    .mobile-menu-icon {
        display: block;
    }
    .li{
        display: block;
    }

}

